@import url(https://fonts.googleapis.com/css?family=Cantarell);
$main-bg: #346;
$button-bg: #7d3232;
$border-color: #ccc;
body {
    font-family: 'Cantarell', Lucida Grande, sans-serif;
    font-size: 100%;
    line-height: 1.5;
    #todo {
        background-color: whitesmoke;
        border-radius: 3px;
        width: 850px;
        margin: 30px auto;
        border: 1px solid #ddd;
    }
    .App {
        h3 {
            background: $main-bg;
            text-align: center;
            color: #fff;
            padding: 10px 15px;
            border-radius: 0 0 2px 2px;
            margin: 0;
            margin-bottom: 20px;
        }
        button {
            border: 1px solid $border-color;
            margin-left: 15px;
            padding: 5px 15px;
            font-size: 1em;
        }
        button.remove-all {
            width: 90%;
            margin: 0 auto;
            display: block;
            margin-top: 15px;
            padding: 10px;
            border-radius: 10px;
            background: #7d3232;
            color: #fff;
            cursor: pointer;
        }
        .todo-list {
            display: inline-block;
            width: 30%;
            padding: 10px;
            vertical-align: top;
            form {
                input[type=text] {
                    border: 1px solid $border-color;
                    padding: 7px 5px;
                    width: 150px;
                    font-size: 14px;
                }
                input[type=submit] {
                    border: 1px solid $border-color;
                    margin-left: 15px;
                    padding: 5px 15px;
                    font-size: 1em;
                }
                .not {
                    color: whitesmoke;
                    background-color: $main-bg;
                    text-align: center;
                }
            }
            .checked {
                text-decoration: line-through;
            }
            .todo-item {
                cursor: pointer;
                margin-bottom: 10px;
                .remove-todo {
                    background-color: $button-bg;
                    font-weight: bold;
                    display: inline-block;
                    margin-left: 10px;
                    padding: 5px;
                    color: #fff;
                    border-radius: 5px;
                }
            }
        }
        .todo-list+.todo-list {
            border-left: 2px solid darkgray;
        }
    }
}